<template>
  <v-form
    ref="form"
    v-model="valid"
    lazy-validation
  >
    <v-text-field
      v-model="name"
      :counter="30"
      :rules="nameRules"
      label="Nom"
      required
    ></v-text-field>

    <v-text-field
      v-model="surname"
      :counter="30"
      :rules="SurnameRules"
      label="Prenom"
      required
    ></v-text-field>

    <v-text-field
      v-model="email"
      :rules="emailRules"
      label="E-mail"
      required
    ></v-text-field>

    <v-select
      v-model="select"
      :items="items"
      :rules="[v => !!v || 'Vous devez mettre un objet']"
      label="Objet"
      required
    ></v-select>

    <v-checkbox
      v-model="checkbox"
      :rules="[v => !!v || 'Accepter pour continuer!']"
      label="Accepter"
      required
      color=#2cbcc4
    ></v-checkbox>

    <v-btn
      :disabled="!valid"
      color=white
      class="mr-4"
      @click="validate"
    >
      Valider
    </v-btn>

    <v-btn
      color=#813b94
      class="mr-4 white--text"
      @click="reset"
    >
      Reset le formulaire
    </v-btn>

    <v-btn
      color=#2cbcc4
      @click="resetValidation"
    >
      Reset la validation
    </v-btn>
  </v-form>
</template>

<script>
  export default {
    data: () => ({
      valid: true,
      name: '',
      nameRules: [
        v => !!v || 'Un nom est nécessaire',
        v => (v && v.length <= 30) || 'Le nom doit être de moins de 30 caractères',
      ],
      SurnameRules: [
        v => !!v || 'Un Prénom est nécessaire',
        v => (v && v.length <= 30) || 'Le prénom doit être de moins de 30 caractères',
      ],
      email: '',
      emailRules: [
        v => !!v || 'un E-mail est nécessaire',
        v => /.+@.+\..+/.test(v) || 'L adresse E-mail doit être valide',
      ],
      select: null,
      items: [
        'Objet 1',
        'Objet 2',
        'Objet 3',
        'Objet 4',
      ],
      checkbox: false,
    }),

    methods: {
      validate () {
        this.$refs.form.validate()
      },
      reset () {
        this.$refs.form.reset()
      },
      resetValidation () {
        this.$refs.form.resetValidation()
      },
    },
  }
</script>